

import axios from "axios";
import { useEffect, useState } from "react";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import '../../styles/createcode.css'
import SidebarAdminMain from "../SidebarAdminMain";
import '../../styles/addadmin.css'
import { useNavigate, useParams } from "react-router-dom";
import swal from "sweetalert";
import { BASE_URL } from "../../App";



function EditAdmin () {

  // const [email, setEmail] = useState("");
  // const [password, setPassword] = useState("");
  // const [fname, setFname] = useState("");
  // const [lname, setLname] = useState("");
  // const [password_confirmation, setPassword_confirmation] = useState("");
  // const [errors, setErrors] = useState("");
  // const [role , setRole] = useState("");
  // const [edit , setEdit] = useState("")
  const navigate = useNavigate();

        const {id} = useParams();
        // console.log(id);


  const token = localStorage.getItem('token');
  const axiosInstance = axios.create({
    baseURL: `${BASE_URL}`
 
});

  const getAdmin = async () => {
      try {
        const token = localStorage.getItem('token');
          const response = await axiosInstance.get(`/api/show-admins/${id}`, {
            headers: {
              Authorization: `Bearer ${token}`,
              'Content-Type': 'application/json',
              "ngrok-skip-browser-warning": "any",
            },
          });
          // console.log(response.data.data.map(item => item.id))
          // console.log(response.data.data[0].attributes.fname);
          // console.log(response.data.data.attributes);

          
          
          setEdit(response.data.data.attributes)
     
          // setFilterCountries(response.data.data)
          const data = response.data.data;
                   return data;
      } catch (error) {
          console.log(error);
      }
  }


  useEffect (() =>{
    getAdmin();
  }, [])

  // console.log(edit);
    const [errors , setErrors] = useState("")
  const [edit, setEdit] = useState({
    fname:"" , 
    lname: "" ,
     email: "", 
      password : "", 
        password_confirmation: "" , 
        role : "",
  });

  const handleEdit = async (event) => {
    event.preventDefault();

    try {
        await axiosInstance.patch(`/api/update-admin/${id}` , {
              ...edit
        } ,
        
        {
            headers: {
                Authorization: `Bearer ${token}`,
                "Content-Type": "application/x-www-form-urlencoded",
                "ngrok-skip-browser-warning": "any",
              },
        }
        );
        setEdit({
          fname:"" , 
          lname: "" ,
        email: "", 
             password : "", 
              password_confirmation: "" , 
              role : "",
        });
        swal({
            title: "   !",
            icon: "success",
            button: "!",
          });
          navigate("/admin/admins");
          // console.log(fname);

    } catch(e) {
        if(e.response.status === 500) {
            // setErrors(e.response.data.errors);
            console.log(e);   
        }
    }
}






  
    return (
        <>
                  <div className="d-flex">
    
       <div className="add-admin ">
      
      <div className="main-title"> </div>
    
      <Container>
    
          <Row>
         
          <Col lg={12}>
                     <div className="quiz-content">
                       <div className="codes">
                       <Form className="align-items-center" onSubmit={handleEdit}>
           
              <div className="input-group">
              <Form.Group className="big-input mb-3 me-lg-5" controlId="formBasicEmail">
        <Form.Label>   </Form.Label>
        <Form.Control type="text" placeholder="  "  name="lname" value={edit.lname}   onChange={(e) => setEdit((prev) => ({ ...prev, lname: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.lname && (
                                <div className="error">{errors.lname[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
              <Form.Group className="big-input mb-3 me-lg-5" controlId="formBasicEmail">
        <Form.Label>   </Form.Label>
        <Form.Control type="text" placeholder="  "  name="fname" value={edit.fname}    onChange={(e) => setEdit((prev) => ({ ...prev, fname: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.fname && (
                                <div  className="error">{errors.fname[0]}</div>
                            )}
        </Form.Text>
       
      </Form.Group>
 
              </div>
              <div className="input-group">
              <Form.Group className="big-input mb-3" controlId="formBasicEmail">
                    <Form.Label>    </Form.Label>
                    <Form.Select aria-label="Default select example" name="role" value={edit.role}   onChange={(e) => setEdit((prev) => ({ ...prev, role: e.target.value }))}>
                    <option >  </option>
                          <option  value="1"> </option>
                           <option value="2"> </option>
    </Form.Select>
    <Form.Text className="text-muted">
        {errors.role && (
                                <div  className="error">{errors.role[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
      
              <Form.Group className="big-input mb-3 me-lg-5" controlId="formBasicEmail">
        <Form.Label>   </Form.Label>
        <Form.Control type="number" placeholder="   " name="email" value={edit.email}   onChange={(e) => setEdit((prev) => ({ ...prev, email: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.email && (
                                <div  className="error">{errors.email[0]}</div>
                            )}
        </Form.Text>
        
      </Form.Group>
              </div>

              <div className="input-group">
              <Form.Group className="big-input mb-3 me-lg-5" controlId="formBasicEmail">
        <Form.Label>     </Form.Label>
        <Form.Control type="password" placeholder="   " name="password_confirmation"  onChange={(e) => setEdit((prev) => ({ ...prev, password_confirmation: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.password_confirmation && (
                                <div  className="error">{errors.password_confirmation[0]}</div>
                            )}
        </Form.Text>
        
      </Form.Group>
        <Form.Group className="big-input mb-3 me-lg-5" controlId="formBasicEmail">
        <Form.Label>    </Form.Label>
        <Form.Control type="password" placeholder="   " name="password"  onChange={(e) => setEdit((prev) => ({ ...prev, password: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.password && (
                                <div  className="error">{errors.password[0]}</div>
                            )}
        </Form.Text>
       
      </Form.Group>
    
              </div>
      
            
       
    
  
      <Button variant="primary" type="submit" className=" ms-0">
        
      </Button>
    </Form>
                        </div> 
              </div>
                   </Col>
          </Row>
      </Container>

    
      </div>
      <div className="sidebar">
       <SidebarAdminMain />
       </div>
       </div>
        </>
        
    )
}

export default EditAdmin;