
import axios from "axios";
import { useEffect, useState } from "react";
import {Accordion ,  Col, Container, Row } from "react-bootstrap";
import Collapsible from "react-collapsible";
import { Link, Navigate, useParams } from "react-router-dom";
import { NavLink } from 'react-router-dom';
import swal from "sweetalert";
import { BASE_URL } from "../../App";
import '../../styles/pay.css'
import  '../../styles/sidebaruser.css'

function Pay() {
    const {id} = useParams()

  const [quizzes , setQuizzes] = useState([]);
  const [videos , setVideos] = useState([]);
  const [homeworks , setHomework] = useState([]);
  const [materials , setMaterials] = useState([]);
  const [video_Duration , setVideo_Duration] = useState([]);
  const [score , setScore] = useState(0)
  const [date , setDate] = useState(0)
  const [details , setDetails] = useState([]);
  const [course , setCourse] = useState([])
  const [degree , setDegree] = useState("")
  const [mindegree , setMinDegree] = useState("")

  
    
useEffect(() => {
          const token = localStorage.getItem('token');
          const config = {
            headers: {
              'Content-Type': 'application/json',
              "ngrok-skip-browser-warning": "any",
              'Authorization': `Bearer ${token}`,
            }
          };       
          axios.get(`${BASE_URL}/api/week-content/${id}` , config)
            .then(response => {
              setQuizzes(response.data.quizzes);
              setVideos(response.data.videos);
              setHomework(response.data.homeworks);
              setMaterials(response.data.materials);
              setDetails(response.data.data);
              // setScore(response.data.quizzes[0].quizResult[0].score);
              // setDate(response.data.quizzes[0].quizResult[0].created_at);
              setDegree(response.data.quizzes.map((props) => props.degree));
              setMinDegree(response.data.quizzes.map((props) => props.mindegree));
          
            })
            .catch(error => {
            if(error.response.status === 500) {
              // console.log(error);
            }
            });
        }, [])

        function convertMinutesToHoursOrMinutes(minutes) {
          if (minutes < 60) {
            return `${minutes} `;
          } else {
            const hours = Math.floor(minutes / 60);
            return `${hours} `;
          }
        }

        function formatDate(dateString) {
          try {
            const date = new Date(dateString);
            const options = {weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', timeZone: 'UTC', numberingSystem: 'arab'};
            const formatter = new Intl.DateTimeFormat('ar-EG', options);
            const formattedDate = formatter.format(date);
            return formattedDate;
          } catch (e) {
            // console.error('Error formatting date', e);
            return '';
          }
         }

            const token = localStorage.getItem('token');
             const axiosInstance = axios.create({
              baseURL: `${BASE_URL}`,
              headers: {
                   'Content-Type': 'application/json',
                   "ngrok-skip-browser-warning": "any",
                     Authorization: `Bearer ${token}`,
                  },
               });
        
        const handleBuyCourses = async (id) => {
          try {
              await axiosInstance.get(`/api/user/enrollWeek/${id}`)
              .then(response => {
                // console.log(response.data);
                setCourse(response.data)
                swal({
                  title: "     !",
                  icon: "success",
                  button: "!",
                });
                setCourse(prevState => ({
                  ...prevState,
                  Owned: true
                }))
              })
              
          } 
          catch(e) {
            if (e.response.status === 401) {
              swal({
                           title: "!     ",
                           icon: "warning",
                           button: "!",
                         });
             }
             else if (e.response.status === 402) {
              swal({
                           title: "!        ",
                           icon: "warning",
                           button: "!",
                         });
             }
             else if (e.response.status === 403) {
              swal({
                           title: "!    ",
                           icon: "warning",
                           button: "!",
                         });
             }
             else if (e.response.status === 405) {
              swal({
                           title: "!       ",
                           icon: "warning",
                           button: "!",
                         });
             }
          }
        }
        // function NestedAccordion(props) {
        //   const [isOpen, setIsOpen] = useState(false);
        
        //   const toggleAccordion = () => {
        //     setIsOpen(!isOpen);
        //   }
        
        //   return (
        //     <div className="accordion-item">
        //       <div className={`accordion-header ${isOpen ? 'open' : ''}`} onClick={toggleAccordion}>
        //         <span>{props.title}</span>
        //         <span className="arrow">{isOpen ? '' : ''}</span>
        //       </div>
        //       <div className={`accordion-content ${isOpen ? 'open' : ''}`}>
        //         {props.children}
        //       </div>
        //     </div>
        //   );
        // }

    return (
        <>  

        <div className="pay pt-5 " >
        {/* <h2 className="main-title"> {}</h2> */}
       <Container>
            <Row className="row ">
            <Col lg={12} className="mb-5"> 
              <div className="main">
              <div className="info">
                <div className="image">
                <img src="/images/bg.3b55416e926d51a05f75.png" alt=""/>
                </div>
                 <div className="info-course d-flex">
                 <div className="content">
                 <h2 className="title">{details.name}  </h2>
             <p>   <span className="date"> {formatDate(details.created_at)}  </span></p>
                 <p>    <span className="date"> {formatDate(details.updated_at)}  </span></p>
                </div>
                <div className="about">
                 <h3> <span>{videos.length}</span>:  </h3>
                 <h3> <span>{quizzes.length}</span>:   </h3>
                 <h3> <span>{homeworks.length}</span>:   </h3>
                 <h3> <span>{materials.length}</span>:   </h3>
                </div>
                 </div>
            
</div>

              </div>
               </Col>
              <Row className="p-0 m-0">

               <Col lg={4}>
              <div className="card-pay">
                <div className="image-pay">
                <img src={`${BASE_URL}/storage/${details.img}`} alt="" class="image-title"/>
                {/* <img src="/images/f1b7a8ca-a93c-416a-9784-370e97e1e0c0.jpg"  className="image-title" alt=""/> */}
                {/* <img src="/images/Logo.jpeg" className="image-title" alt=""/> */}
                </div>
                <div className="price d-flex">
                  <span className="ms-2"> {details.price}.00 </span>
                  <span> </span>
                </div>
                <div class="button-pay">

                    {
                      details.price === 0 
                      ?
                      <button class="button-57" role="button" style={{ pointerEvents: "none" , backgroundColor: "#f43f5e" , color: "#fff"}}><span class="text" >    </span><span>    </span></button>
                        :
                      details.Owned
                      ? 
                      <button class="button-57" role="button" style={{ pointerEvents: "none" , backgroundColor: "#f43f5e" , color: "#fff"}}><span class="text" >    </span><span>     </span></button>
                        :
                        <button class="button-57" role="button" onClick={ () => alert("    ") | handleBuyCourses(id) }><span class="text" >  </span><span>  </span></button>
                    }

                    </div>
                <div className="info-pay">
                  <div className="d-flex">
                    <span></span>
                   <div class="watch-count d-flex">
                    <span>{convertMinutesToHoursOrMinutes(details.videoMinutes)}</span>
                   {/* <span className="me-2"> </span> */}
                   </div>
                  </div>
                  <div className="d-flex mt-4">
                    <span> </span>
                    <div class="watch-count d-flex">
                    <span> 5</span>
                   <span className="me-2"> </span>
                   </div>
                  </div>
                </div>
              </div>
            </Col>

            <Col lg={8}>
              <div className="image-main-title">
              <img src={`${BASE_URL}/storage/${details.img}`} alt="" class="image-title"/>
              </div>
            </Col>
              </Row>
            <Row className="m-0 p-0" >
              <Col lg={12}>
              <Accordion defaultActiveKey="0" flush className="accord">
      <Accordion.Item eventKey="1">
        <Accordion.Header>
          <h2>  <span>{details.name}</span></h2>
        </Accordion.Header>    
        <Accordion.Body>
       
          <div className="main-body">
          {
            quizzes.map((quiz) =>{
                return (
                  <div className="quizess" key={quiz.id}>
                  <Accordion.Header>
                  <div className="body-title d-flex align-items-center">
                <h2>
                      {quiz.title}
                </h2>
                <i class="fa-solid fa-file-circle-question"></i>
                  </div>
                  {/* <button className="button-quiz ms-5" role="button">  </button> */}
                  </Accordion.Header>
                <Accordion.Body>
                      <div className="body-accord d-flex">

                       {
                        details.Owned
                        ?
                        
                          quiz.score === ""
                          ?
                          <Link to={`/startquiz/${quiz.id}`} className="quiz-btn"> </Link>
                          :
                          <Link to={`/modelanswerquiz/${quiz.id}`} className="quiz-btn" >{`${quiz.score } /  ${quiz.degree}`}</Link>
                        :
                        details.price === 0  
                        ?
                        <Link to={`/chargewallet`}  className="quiz-btn">   </Link>
                        :
                        <Link to={`/chargewallet`}  className="quiz-btn">   </Link>
                       }

                        <div className="content-1">
                          <p>  :<span className="ms-2">{quiz.title}</span></p>
                          <p> : <span className="ms-2">{quiz.minutes}</span></p>
                          <p> : <span className="ms-2">{quiz.degree}</span></p>
                          <p>  : <span className="ms-2">{quiz.mindegree}</span> </p>
                          {
                            quiz.score === ""
                            ?
                            ""
                            :
                            <p>   : <span className="ms-2">{quiz.score}</span> </p>
                          }
                    {/* {
                      quizzes[0].quizResult === 0 ? "" : 
                       <>
                       <p>  :
                      <span className="ms-2">{score}</span></p> 
                      <p>   : <span className="ms-2">{formatDate(date)} </span> </p>
                      </>
                    } */}
            
                        </div>
                      </div>
                </Accordion.Body>
                  </div>
                )
            })
          }
        
            {
              videos.map((video) => {
                return (
                  <div className="videos" key={video.id}>
                  <Accordion.Header>
                  <div className="body-title d-flex align-items-center">
                <h2>
                      {video.title}
                </h2>
                <i class="fa-solid fa-video"></i>
                  </div>
                  {/* <button className="button-quiz ms-5" role="button">  </button> */}
                  </Accordion.Header>
                <Accordion.Body>
                      <div className="body-accord d-flex">
                      {
                        details.Owned
                        ?
                        video.count === video.noviews 
                        ?
                        <Link  className="video-btn"> </Link>
                        :
                    
                        
                        <Link to={`/video/${video.id}`} className="video-btn"> </Link>

                        :
                        details.price === 0  
                        ?
                        <Link to={`/chargewallet`}  className="video-btn">   </Link>
                        :
                        <Link to={`/chargewallet`}  className="video-btn">   </Link>
                       }
                        <div className="content-2">
                          <p>  :<span>{video.title}</span></p>
                          <p> : <span>{video.video_dauration} </span></p>
                          <p>    : <span>{video.noviews}</span></p>
                          {
                            video.count === ""
                            ?
                            ""
                            :
                            <p>   : <span>{video.count}</span></p>
                          }
                          {/* <p>  : <span>60</span></p> */}
                        </div>
                      </div>
                </Accordion.Body>
                  </div>
                )
              })
            }

          {
            homeworks.map((homework) => {
                  return(
                    <div className="Homework" key={homework.id}>
          <Accordion.Header>
          <div className="body-title d-flex align-items-center">
        <h2>
                      {homework.title}
        </h2>
        <i class="fa-solid fa-file-circle-question"></i>
          </div>
          {/* <button className="button-quiz ms-5" role="button">  </button> */}
            </Accordion.Header>
           <Accordion.Body>
              <div className="body-accord d-flex">
                     
                       {
                        details.Owned
                        ?
                        <Link to={`/starthomework/${homework.id}`} className="quiz-btn"> </Link>
                        :
                        details.price === 0  
                        ?
                        <Link to={`/chargewallet`}  className="quiz-btn">   </Link>
                        :
                        <Link to={`/chargewallet`}  className="quiz-btn">   </Link>
                       }

                <div className="content-1">
                  <p>  :<span>{homework.title}</span></p>
                  <p>  : <span>{homework.degree}</span>   </p>
                  <p> : <span className="ms-2">{homework.minutes}</span></p>
                  {
                            homework.score === ""
                            ?
                            ""
                            :
                            <p>   : <span className="ms-2">{homework.score}</span> </p>
                          }
                </div>
              </div>
        </Accordion.Body>
          </div>
                  )
            })
          }


              {
                materials.map((material) => {
                  return (
                    <div className="material" key={material.id}>
          <Accordion.Header>
          <div className="body-title d-flex align-items-center">
        <h2>
                        {material.title}
        </h2>
         <i class="fa-solid fa-file-pdf"></i>
          </div>
          {/* <button className="button-quiz ms-5" role="button">  </button> */}
          </Accordion.Header>
        <Accordion.Body>
              <div className="body-accord d-flex">
              <Link to={`/startpdf/${material.id}`} className="video-btn"> </Link>
                <div className="content-2">
                  <p>  :<span>{material.title}</span></p>
                </div>
              </div>
            </Accordion.Body>
            </div>
                  )
                })
              }

          </div>
        </Accordion.Body>
      </Accordion.Item>
     </Accordion>



    {/* <div className="accordion">
      <NestedAccordion title="Accordion 1">
        <NestedAccordion title="Nested Accordion 1">
          <p>Nested Content 1</p>
        </NestedAccordion>
        <NestedAccordion title="Nested Accordion 2">
          <p>Nested Content 2</p>
        </NestedAccordion>
      </NestedAccordion>

    </div> */}
              </Col>
            </Row>      
            </Row>
        </Container>
        </div>
 
        </>
    )
}
export default Pay;