import Aos from "aos";
import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import { Typewriter } from "react-simple-typewriter";
import { BASE_URL } from "../App";
import '../styles/homepage.css'


function Homepage ({loggedIn , auth}) {
    
   
    const [details, setDetails] = useState([]);

    useEffect(() => {
      const token = localStorage.getItem('token');
    
      const config = {
        headers: {
          'Content-Type': 'application/json',
          "ngrok-skip-browser-warning": "any",
          'Authorization': `Bearer ${token}`,
        }
      };
    
      axios.get(`${BASE_URL}/api/level`, config)
        .then(response => {
        //   console.log(response.data.data);
          setDetails(response.data.data);
        })
        .catch(error => {
        //   console.error(error);
        });
    }, []);
    



    return (
        <>
                    <div className="landing d-flex align-items-center justify-content-center">
    <Container>
        <Row className="align-items-center">
        <Col lg={5}>
            <div className="image">
                {/* <img src="/images/WhatsApp Image 2023-02-26 at 12.10.53.png" alt=""/> */}
                <img src="/images/Mohamedsaad.png" />
            </div>
        </Col>
        <Col lg={7}>
         <h1 className="type">
         <Typewriter
            loop
            cursor
            cursorStyle="_"
            typeSpeed={200}
            deleteSpeed = {100}
            delaySpeed= {4000}
            words={["Mr/ Mohamed Saad "]}
            />
         </h1>
           <div className="text">
           <h3>      <span> </span></h3>
           {
            
            loggedIn ?  
     
            (
                <Link to="/numberphone" className="d-block text-center sub-2">
                 
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                </Link>
                )
            :
              <>
              <Link to={'/registeruser'} className="d-block text-center sub">
              ! 
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              </Link>
                <Link to="/numberphone" className="d-block text-center sub-2">
                 
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            </Link>
              </>
   }
  
           </div>
        
        </Col>
        </Row>
    </Container>
    </div>


    <div className="feature pt-5">

            <Container>
            <h1 className="text-center mb-5 main-title"> </h1>
                <Row className="">
                <Col lg={4}>
                    <div className="boxl-1">
                    <div className="box">
               <img src="/images/4891584.jpg" alt=""/>
               </div>
               <h2>   </h2>
                    </div>
                </Col>
                <Col lg={4}>
                <div className="boxl-2">
                <div className="box">
               <img src="/images/8177774.jpg" alt=""/>
               </div>
               <h2>    </h2>
                </div>
                </Col>
                <Col lg={4}>
                <div className="boxl-3">
                <div className="box">
               <img src="/images/106771-OMUS26-706.jpg" alt=""/>
               </div>
               <h2>   </h2>
                </div>
                </Col>
                </Row>
            </Container>
        </div>

        <div className="main-courses">
            <div className="waves">
            <h2></h2>
            <div className="wave wave-1"></div>
            <div className="wave wave-2"></div>
            <div className="wave wave-3"></div>
            <div className="wave wave-4"></div>
        </div>
        <h2 className="edu" > <span></span></h2>
        <div className="courses">
        <Container>
            <Row>

                    {
                        details.map((props) => {
                            return(
                                <Col lg={4} md={12} key={props.id}>
                                <div className="card">
                                    <div className="lines"></div>
                                    <div className="imgBx">
                                        {/* <img src="/images/f1b7a8ca-a93c-416a-9784-370e97e1e0c0.jpg" alt="" /> */}
                                        <img src="/images/7460147.jpg" alt=""/>
                                    </div>
                                    <div className="content">
                                        <div className="details">
                                            
                                            <h2> <span>{props.title}</span> </h2>
            
                                            <div className="data">
                                                <span className="data-content"><span>{props.weeks}</span> : </span>
                                                <span className="data-content"><span>{props.videos}</span> : </span>
                                                <span className="data-content"><span>{props.questions}</span> :    </span>
                                                <span className="data-content"><span>{props.quizzes}</span> : </span>
                                                <span className="data-content"><span>{props.homeworks}</span> : </span>
                                            </div>
                                            <div className="actionBtn">
                                                    {
                                                        loggedIn 
                                                        ?
                                                        <>
                                                         {
                                                            auth.level === props.id
                                                            ?
                                                            <Link className="last" to={`/mycourses`}> </Link>
                                                            :
                                                            <Link className="last" > </Link>
                                                         }
                                                        </>
                                                        :
                                                 <>
                                                        <Link className="first" to="/registeruser"> </Link>
                                                <Link className="last" to={`/courses/${props.id}`}> </Link>
                                                 </>
                                                    }
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Col>
                            )
                        })
                    }



                {/* <Col lg={4} md={12}>
                    <div className="card">
                        <div className="lines"></div>
                        <div className="imgBx">
                            <img src="/images/Logo.jpeg" alt="" />
                        </div>
                        <div className="content">
                            <div className="details">
                                <h2> <span></span> </h2>

                                <div className="data">
                                    <span className="data-content"><span>1</span> : </span>
                                    <span className="data-content"><span>1</span> : </span>
                                    <span className="data-content"><span>1</span> :    </span>
                                    <span className="data-content"><span>1</span> : </span>
                                </div>
                                <div className="actionBtn">
                                    <Link to="register"> </Link>
                                    <Link> </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </Col>
                <Col lg={4} md={12}>
                    <div className="card">
                        <div className="lines"></div>
                        <div className="imgBx">
                            <img src="/images/Logo.jpeg" alt="" />
                        </div>
                        <div className="content">
                            <div className="details">
                                <h2> <span></span> </h2>

                                <div className="data">
                                    <span className="data-content"><span>1</span> : </span>
                                    <span className="data-content"><span>1</span> : </span>
                                    <span className="data-content"><span>1</span> :    </span>
                                    <span className="data-content"><span>1</span> : </span>
                                </div>
                                <div className="actionBtn">
                                    <Link to="register"> </Link>
                                    <Link to="courses"> </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </Col> */}
            </Row>
        </Container>
        </div>
            </div>

       

        </>
    )
}

export default Homepage;