

import axios from 'axios';
import { useState } from 'react';
import { Button, Col, Container, Form, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router';
import { Link } from 'react-router-dom';
import swal from 'sweetalert';
import { BASE_URL } from '../App';
import '../styles/registeruser.css'


function Registeruser() {

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [fname, setFname] = useState("");
    const [lname, setLname] = useState("");
    const [pphone, setPphone] = useState("");
    const [phone, setPhone] = useState("");
    const [level, setLevel] = useState("");
    const [government, setGovernment] = useState("");
    const [password_confirmation, setPassword_confirmation] = useState("");
    const [errors, setErrors] = useState("");
    const navigate = useNavigate()

    const handleRegister = async (event) => {
        event.preventDefault();
        try {
            await axios.post(`${BASE_URL}/api/register` , {
                email, 
                password , 
                fname , 
                lname , 
                pphone ,
                 phone ,
                  level , 
                  government , 
                  password_confirmation , 
                 
            });
            setEmail("");
            setPassword("");
            setFname("");
            setLname("");
            setPphone("");
            setPhone("");
            setLevel("");
            setGovernment("");
            setPassword_confirmation("");
            swal({
                title: "   !",
              
                icon: "success",
                button: "!",
              });
              navigate("/loginuser")

        } catch(e) {
            if(e.response.status === 422) {
                setErrors(e.response.data.errors);
                // console.log(e);  
            }
        }
    }

    return (
        <>
        <div className="register-user pt-5 pb-5">
        <div className="main-title"> </div>
            <Container>  
                <Row>
                    <Col lg={12}>
                        <div className="box mt-2">
                        <Form onSubmit={handleRegister}>
                    <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicText">
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder="  " name="lname" onChange={(e) =>setLname(e.target.value)}/>
        <Form.Text className="text-muted">
        {errors.lname && (
                                <div className="error">{errors.lname[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
                    <Form.Group className="big-input mb-3" controlId="formBasicText" >
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder="  " name="fname" onChange={(e) =>setFname(e.target.value)}/>
        <Form.Text className="text-muted">
        {errors.fname && (
                                <div  className="error">{errors.fname[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
  
                    </div>
                    <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicPhone" >
        <Form.Label>   </Form.Label>
        <Form.Control type="number" placeholder="     " name="pphone" onChange={(e) =>setPphone(e.target.value)}/>
        <Form.Text className="text-muted">
        {errors.pphone && (
                                <div className="error">{errors.pphone[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3 big-input" controlId="formBasicPhone" >
        <Form.Label>  </Form.Label>
        <Form.Control type="number" placeholder="    " name="email" onChange={(e) =>setEmail(e.target.value)}/>
        <Form.Text className="text-muted">
        {errors.email && (
                                <div  className="error">{errors.email[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
                    </div>
                    <Form.Group className="mb-3 w-100" controlId="formBasicPassword">
        <Form.Label>   </Form.Label>
        <Form.Control type="email"  required  placeholder="  " name="phone" onChange={(e) =>setPhone(e.target.value)}/>
        <Form.Text className="text-muted">
        {errors.phone && (
                                <div  className="error">{errors.phone[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
       <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicPassword">
                    <Form.Label>  </Form.Label>
                    <Form.Select aria-label="Default select example" name="government" onChange={(e) =>setGovernment(e.target.value)}>

      <option > </option>
 <option value=""></option>
 <option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=" "> </option>
<option value =" "> </option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
    </Form.Select>
    <Form.Text className="text-muted">
        {errors.government && (
                                <div  className="error">{errors.government[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
                    <Form.Group className="big-input mb-3" controlId="formBasicEmail">
                    <Form.Label>   </Form.Label>
                    <Form.Select aria-label="Default select example" name="level" onChange={(e) =>setLevel(e.target.value)}>
                    <option >  </option>
                          <option  value="1">  </option>
                           <option value="2">  </option>
                           <option value="3">  </option>
    </Form.Select>
    <Form.Text className="text-muted">
        {errors.level && (
                                <div  className="error">{errors.level[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
  
                    </div>
                    <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicPassword">
        <Form.Label>   </Form.Label>
        <Form.Control type="password" placeholder="  " name="password_confirmation" onChange={(e) =>setPassword_confirmation(e.target.value)}/>
        <Form.Text className="text-muted">
        <Form.Text className="text-muted">
        {errors.password && (
                                <div  className="error">{errors.password[0]}</div>
                            )}
        </Form.Text>
        </Form.Text>
      </Form.Group>
                    <Form.Group className="big-input mb-3" controlId="formBasicEmail">
        <Form.Label> </Form.Label>
        <Form.Control type="password" placeholder="   " name="password" onChange={(e) =>setPassword(e.target.value)}/>
        <Form.Text className="text-muted">
        <Form.Text className="text-muted">
        {errors.password_confirmation && (
                                <div  className="error">{errors.password_confirmation[0]}</div>
                            )}
        </Form.Text>
        </Form.Text>
      </Form.Group>
                    </div>
     
                    <Button variant="outline-primary" className="mt-3 mb-3" type="submit"> </Button>{' '}
      <Link to="/loginuser">!   </Link>
    </Form>
 
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
        </>
    )
    
}

export default Registeruser;