import { Col, Container, Row, Table } from "react-bootstrap";
import "../../styles/profilequiz.css";
import DataTable from "react-data-table-component";
import { useEffect, useState } from "react";
import { height, width } from "@mui/system";
import axios from "axios";
import styled from "@emotion/styled";
import { colors } from "@mui/material";
import { Link , useParams} from "react-router-dom";
import { BASE_URL, tableCustomStyles } from "../../App";
import SidebarProfileWithAdmin from "./SidebarProfileWithAdmin";
// import '../styles/table.css'

function StudentProfileHomeworks() {
    const {id} = useParams()
  const [homework , setHomework] = useState([]);
  const [filteredData, setFilteredData] = useState([]);

  const axiosInstance = axios.create({
    baseURL: `${BASE_URL}`,
    headers: {
        'Content-Type': 'application/json',
        "ngrok-skip-browser-warning": "any",
    },
});


const getQuizData = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await axiosInstance.get(`/api/student-homeworks/${id}` , {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        // console.log(response.data.data.map(item => item.id))
        console.log(response.data.data);
        setHomework(response.data.data)
        setFilteredData(response.data.data);
        const data = response.data.data;
                 return data;
    } catch (error) {
        console.log(error);
    }
}

useEffect (() =>{
  getQuizData();
}, [])


  const columns = [
    {
      name: "ID",
      selector: (row) => row.id,
      sortable : true,

    },
    {
      name: "Week_ID",
      selector: (row) => row.week_id,
      sortable : true,
    
    },
    {
      name: "Name",
      selector: (row) => row.title,
    
    },
    {
      name: "Minutes",
      selector: (row) => row.minutes,
    //   style: {
    //     background: "#e6e6e6",
    //   }
    },
    {
      name: "Degree",
      selector: (row) => row.degree,
    //   style: {
    //     background: "#e6e6e6",
    //   }
    },
    {
      name: "Mindegree",
      selector: (row) => row.mindegree,
    //   style: {
    //     background: "#e6e6e6",
    //   }
    },
    {
      name: "Score",
      selector: (row) => row.pivot.score,
   
    },
    {
      name: "Start",
      selector: (row) => row.start,
      grow:3
    },
    {
      name: "End",
      selector: (row) => row.end,
      grow:3
    },
    {
      name: "Level",
      selector: (row) => row.level,
      
    },

  
  ];

  return (
    <>
            <div className="profile-quiz">
            <Container>
                <Row>
                    <Col lg={12}>
                        <div className="profile-ground">
                                <img src="/images/bg.3b55416e926d51a05f75.png" alt=""/>
                          
                        </div>
                        <div className="profile-user pt-5">
                                <div className="main-title mt-5">   </div>
                                <Container>
                                <Row >
                                    <Col lg={12} className="mt-2">
                                  <div className="main-info">

                                        <Container>
                                            <Row className="" >
                                          <Col lg={8}>
                                          <div className="quiz-content">
              <DataTable
                             customStyles={tableCustomStyles}

               columns={columns}
                data={filteredData} 
                pagination 
                fixedHeader
                // selectableRows
                // fixedHeaderScrollHeight="400px"
                highlightOnHover
                // actions={
                //     <button className="btn btn-info">Export</button>
                // }
                subHeader
                
                />
              </div>
                                          </Col>
                                          <Col lg={4}>
                                  <SidebarProfileWithAdmin />
                                    </Col>

                                            </Row>
                                        </Container>

                                  </div>

                                    </Col>
                                
                               
                                </Row>
                                </Container>
                            </div>
                    
                    </Col>
                </Row>

            </Container>
        </div>
    </>
  );
}

export default StudentProfileHomeworks;


