
import { Link } from "@material-ui/core";
import { DefaultPlayer as Video } from "react-html5video/dist";
import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useNavigate, useParams  } from "react-router-dom";
import swal from "sweetalert";
import 'react-html5video/dist/styles.css'
import '../../styles/video.css'
import {BASE_URL} from "../../App"


// src={intro_video} 

function VideoPlayer () {
  
  const navigate = useNavigate()
  const {id} = useParams()
  // console.log(id);
      
    // function NestedAccordion(props) {
    //     const [isOpen, setIsOpen] = useState(false);
      
    //     const toggleAccordion = () => {
    //       setIsOpen(!isOpen);
    //     }
      
    //     return (
    //       <div className="accordion-item">
    //         <div className={`accordion-header ${isOpen ? 'open' : ''}`} onClick={toggleAccordion}>
    //           <span>{props.title}</span>
    //           <span className="arrow">{isOpen ? '' : ''}</span>
    //         </div>
    //         <div className={`accordion-content ${isOpen ? 'open' : ''}`}>
    //           {props.children}
    //         </div>
    //       </div>
    //     );
    //   }


    const [video ,setVideo] = useState("");
    useEffect(() => {
      const token = localStorage.getItem('token');
      const config = {
        headers: {
          'Content-Type': 'application/json',
          "ngrok-skip-browser-warning": "any",
          'Authorization': `Bearer ${token}`,
        }
      };       
      axios.get(`${BASE_URL}/api/user/enrollVideo/${id}` , config)
        .then(response => {
          // console.log(response.data.data);
          setVideo(response.data.data);
        })
        .catch(error => {
          console.error(error);
        });
    }, [id])
    

          
    return (
        <>
                <div className="video pt-5 pb-5">
                <div className="main-title"> {video.title} </div>
                    <Container>
                        <Row>
                            <Col>
                                    <div className="video-play mt-2 mb-5">

                                    <div className="video-iframe">
                                 
                                      {
                                        video.video_path === null 
                                        ?
                                        <iframe width="560" height="315" src={video.iframe} title={video.title} frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
                                          :
                                                <Video>
                                       <source src={`${BASE_URL}/storage/${video.video_path}`}  type="video/webm"/>
                                                  </Video>
                                      }

                                    </div>
                           
                                    </div>
                                    {/* <div className="accordion">
      <NestedAccordion title="Accordion 1">
        <NestedAccordion title="Nested Accordion 1">
          <p>Nested Content 1</p>
        </NestedAccordion>
        <NestedAccordion title="Nested Accordion 2">
          <p>Nested Content 2</p>
        </NestedAccordion>
      </NestedAccordion>
      <NestedAccordion title="Accordion 2">
        <NestedAccordion title="Nested Accordion 3">
          <p>Nested Content 3</p>
        </NestedAccordion>
        <NestedAccordion title="Nested Accordion 4">
          <p>Nested Content 4</p>
        </NestedAccordion>
      </NestedAccordion>
    </div> */}
                                    
                            </Col>
                        </Row>
                    </Container>
                </div>
        </>
    )
}

export default VideoPlayer;