
import axios from "axios";
import { useEffect, useState } from "react";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { Link, useNavigate, useParams } from "react-router-dom";
import swal from "sweetalert";
import '../../styles/addvideo.css'
import SidebarAdminMain from "../SidebarAdminMain";
import { BASE_URL } from "../../App";



function EditWeek() {
        
  const { id } = useParams();
    const navigate = useNavigate();
  
    const [week , setWeek] = useState([]);
  const token = localStorage.getItem('token' );

    const axiosInstance = axios.create({
      baseURL: `${BASE_URL}`,
  });

  const getWeek = async () => {
    try {
      const token = localStorage.getItem('token' );
      const response = await axiosInstance.get(`/api/user/weeks/${id}` , {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json',
            "ngrok-skip-browser-warning": "any",
          },
        });
        // console.log(response.data.data.map(item => item.id))
        console.log(response.data.data.attributes);
        setEdit(response.data.data.attributes)
        // setFilteredData(response.data.data);
        const data = response.data.data;
                 return data;
    } catch (error) {
        console.log(error.response);
    }
}

useEffect (() =>{
  getWeek();
}, [])


const [edit, setEdit] = useState({
    name: "",
    description: "",
    price: "",
    img: "",
    level: "",
    created_at: "",
    updated_at: "",
});
const [errors, setErrors] = useState({});

  const handleEdit = async (event) => {
    event.preventDefault();
    // const token = sessionStorage.getItem('token' );
    try {
      await axiosInstance.patch(
        `/api/weeks/${id}`,
        {
          ...edit,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/x-www-form-urlencoded",
            "ngrok-skip-browser-warning": "any",
          },
        }
      );
      setEdit({
        name: "",
    description: "",
    price: "",
    img: "",
    level: "",
    created_at: "",
    updated_at: "",
      });
      swal({
        title: "   !",
        icon: "success",
        button: "!",
      });
      navigate("/admin/weekdata");
     
    } catch (e) {
      if (e.response.status === 200) {
        
        console.log(e);
        setErrors(e.response.data.errors);
      }
    }
  };
  
 
  


  console.log(edit);

  
  
    
    return (
        <>
      <div className="d-flex">
    
       <div className="add-video">
      
      <div className="main-title">     {id}  </div>
    
      <Container>
    
          <Row>
         
          <Col lg={12}>
                     <div className="quiz-content">
                    <div className="video">

                        <div className="create-box">
                        <h2 className="title mt-4 mb-4">  </h2>
                        <Form onSubmit={handleEdit}>
         


     <div className="chooices d-lg-flex align-items-center">
     <Form.Group className="mb-3 me-lg-3 videoInput" controlId="formBasicText">
        <Form.Label>  </Form.Label>
        <Form.Control type="text" placeholder="  " name="name" value={edit.name}  onChange={(e) => setEdit((prev) => ({ ...prev, name: e.target.value }))}/>
      </Form.Group>
     <Form.Group className="mb-3 videoInput" controlId="formBasicText ">
        <Form.Label>   </Form.Label>
        <Form.Control type="file" name="img"  onChange={(e) => setEdit((prev) => ({ ...prev, img: e.target.value }))}/>
      </Form.Group>
    
     </div>

     <div className="chooices d-lg-flex align-items-center">
     <Form.Group className="mb-3 videoInput me-lg-3" controlId="formBasicText ">
        <Form.Label>    </Form.Label>
        <Form.Control type="text" placeholder="  " name="description" value={edit.description}  onChange={(e) => setEdit((prev) => ({ ...prev, description: e.target.value }))}/>
      </Form.Group>
     <Form.Group className="mb-3 videoInput" controlId="formBasicText">
        <Form.Label>  </Form.Label>
        <Form.Control type="number" placeholder="  " name="price" value={edit.price}  onChange={(e) => setEdit((prev) => ({ ...prev, price: e.target.value }))}/>
      </Form.Group>
   
     </div>
    

        
  

     <div className="chooices d-lg-flex align-items-center mt-lg-4">
   

    <Form.Select aria-label="Default select example" className="mt-3" name="level" value={edit.level}  onChange={(e) => setEdit((prev) => ({ ...prev, level: e.target.value }))}>
      <option>  </option>
      <option value="1">  </option>
      <option value="2">  </option>
      <option value="3">  </option>
    </Form.Select>
     </div>

    
 




      
        

      <Button variant="primary" type="submit" className="create-button">
        
      </Button>
    </Form>
    </div>
                    </div>
              </div>
               </Col>
          </Row>
      </Container>

    
      </div>
      <div className="sidebar">
       <SidebarAdminMain />
       </div>
       </div>
        </>
    )
}

export default EditWeek;