


import axios from 'axios';
import { useEffect, useState } from 'react';
import { Button, Col, Container, Form, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router';
import { Link, useParams } from 'react-router-dom';
import swal from 'sweetalert';
import { BASE_URL } from '../../App';
import '../../styles/registeruser.css'


function Editstudent() {

    // const [email, setEmail] = useState("");
    // const [password, setPassword] = useState("");
    // const [fname, setFname] = useState("");
    // const [lname, setLname] = useState("");
    // const [pphone, setPphone] = useState("");
    // const [phone, setPhone] = useState("");
    // const [level, setLevel] = useState("");
    // const [government, setGovernment] = useState("");
    // const [password_confirmation, setPassword_confirmation] = useState("");
    // const [errors, setErrors] = useState("");
    // const [edit , setEdit] = useState("")
    const navigate = useNavigate()

    // const csrf = () => axios.get("/sanctum/csrf-cookie");
    const {id} = useParams();

    const token = localStorage.getItem('token' );
    const axiosInstance = axios.create({
        baseURL: `${BASE_URL}`,
    });
    
    
    
    const getStudent = async () => {
        try {
          const token = localStorage.getItem('token' );
            const response = await axiosInstance.get(`/api/user/${id}`, {
              headers: {
                Authorization: `Bearer ${token}`,
                'Content-Type': 'application/json',
                "ngrok-skip-browser-warning": "any",
              },
            });
            // console.log(response.data.data.map(item => item.id))
            // console.log(response.data.data);
            // console.log(response.data.data.attributes);
            setEdit(response.data.data.attributes)
            // setFilterCountries(response.data.data)
            const data = response.data.data;
                     return data;
        } catch (error) {
            console.log(error);
        }
    }
  

    useEffect(() =>{
      getStudent();
    }, [])


const [errors , setErrors] = useState("")
const [edit, setEdit] = useState({
  fname: "" , 
    lname: "" , 
    email: "", 
  password: "" , 
  pphone: "" ,
    phone : "",
    level : "", 
     government : "", 
      password_confirmation: "" , 
});

    const handleEditStudent = async (event) => {
        event.preventDefault();
        // await csrf();
        try {
  await axiosInstance.patch(`/api/user/${id}` , {
      ...edit,
                 
            }, 
            {
                headers: {
                    Authorization: `Bearer ${token}`,
                    "Content-Type": "application/x-www-form-urlencoded",
                    "ngrok-skip-browser-warning": "any",
                  },
            });
            setEdit({
              fname: "" , 
              lname: "" , 
               email: "", 
              password: "" , 
             pphone: "" ,
              phone : "",
               level : "", 
               government : "", 
             password_confirmation: "" , 
          });
            swal({
                title: "     !",
              
                icon: "success",
                button: "!",
              });
              navigate("/admin/allstudentdata")

              

          
        } catch(e) {
            if(e.response.status === 422) {
                setErrors(e.response.data.errors);
                console.log(e);  
            }
        }
    }

  
      
    return (
        <>
        <div className="register-user pt-5 pb-5">
        <div className="main-title"> </div>
            <Container>
                
                <Row>
                    <Col lg={12}>
                        <div className="box mt-2">
                        <Form onSubmit={handleEditStudent}>
                    <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicText">
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder="  " name="lname" value={edit.lname}  onChange={(e) => setEdit((prev) => ({ ...prev, lname: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.lname && (
                                <div className="error">{errors.lname[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
                    <Form.Group className="big-input mb-3" controlId="formBasicText" >
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder="  " name="fname" value={edit.fname}  onChange={(e) => setEdit((prev) => ({ ...prev, fname: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.fname && (
                                <div  className="error">{errors.fname[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
  
                    </div>
                    <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicPhone" >
        <Form.Label>   </Form.Label>
        <Form.Control type="number" placeholder="     " value={edit.pphone} name="pphone" onChange={(e) => setEdit((prev) => ({ ...prev, pphone: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.pphone && (
                                <div className="error">{errors.pphone[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3 big-input" controlId="formBasicPhone" >
        <Form.Label>  </Form.Label>
        <Form.Control type="number" placeholder="    " value={edit.email}  name="email"  onChange={(e) => setEdit((prev) => ({ ...prev, email: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.email && (
                                <div  className="error">{errors.email[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
                    </div>
                    <Form.Group className="mb-3 w-100" controlId="formBasicPassword">
        <Form.Label>   </Form.Label>
        <Form.Control type="email"  required  placeholder="  " value={edit.phone} name="phone"  onChange={(e) => setEdit((prev) => ({ ...prev, phone: e.target.value }))}/>
        <Form.Text className="text-muted">
        {errors.phone && (
                                <div  className="error">{errors.phone[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
       <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicPassword">
                    <Form.Label>  </Form.Label>
                    <Form.Select aria-label="Default select example" name="government" value={edit.government} onChange={(e) => setEdit((prev) => ({ ...prev, government: e.target.value }))}>

      <option > </option>
 <option value=""></option>
 <option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=" "> </option>
<option value =" "> </option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=" "> </option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
<option value=""></option>
    </Form.Select>
    <Form.Text className="text-muted">
        {errors.government && (
                                <div  className="error">{errors.government[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
                    <Form.Group className="big-input mb-3" controlId="formBasicEmail">
                    <Form.Label>   </Form.Label>
                    <Form.Select aria-label="Default select example" name="level" value={edit.level} onChange={(e) => setEdit((prev) => ({ ...prev, level: e.target.value }))}>
                    <option >  </option>
                          <option  value="1">  </option>
                           <option value="2">  </option>
                           <option value="3">  </option>
    </Form.Select>
    <Form.Text className="text-muted">
        {errors.level && (
                                <div  className="error">{errors.level[0]}</div>
                            )}
        </Form.Text>
      </Form.Group>
  
                    </div>
                    <div className="input-group">
                    <Form.Group className="mb-3 big-input" controlId="formBasicPassword">
        <Form.Label>   </Form.Label>
        <Form.Control type="password" placeholder="  " name="password_confirmation" onChange={(e) => setEdit((prev) => ({ ...prev, password_confirmation: e.target.value }))}/>
        <Form.Text className="text-muted">
        <Form.Text className="text-muted">
        {errors.password_confirmation && (
                                <div  className="error">{errors.password_confirmation[0]}</div>
                            )}

        </Form.Text>
        </Form.Text>
      </Form.Group>
                    <Form.Group className="big-input mb-3" controlId="formBasicEmail">
        <Form.Label> </Form.Label>
        <Form.Control type="password" placeholder="   " name="password" onChange={(e) => setEdit((prev) => ({ ...prev, password: e.target.value }))}/>
        <Form.Text className="text-muted">
        <Form.Text className="text-muted">
        {errors.password && (
                                <div  className="error">{errors.password[0]}</div>
                            )}
        </Form.Text>
        </Form.Text>
      </Form.Group>
  
                    </div>
     
      <Button variant="primary" className="mt-3 mb-3"  type="submit">
         
      </Button>
      {/* <Link to="/loginuser">!   </Link> */}
    </Form>
 
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
        </>
    )
    
}

export default Editstudent;


