

import axios from "axios";
import { useEffect, useState } from "react";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { Link, Navigate, useNavigate, useParams } from "react-router-dom";
import '../../styles/addquestion.css'
import SidebarAdminMain from "../SidebarAdminMain";
import swal from "sweetalert";
import { BASE_URL } from "../../App";


function EditQuestion() {

    // console.log(id);


    // const [img , setImg] = useState("")
    // const [question , setQuestion] = useState("")
    // const [level , setLevel] = useState("")
    // const [c1 , setC1] = useState("")
    // const [c2 , setC2] = useState("")
    // const [c3 , setC3] = useState("")
    // const [c4 , setC4] = useState("")
    // const [c1_img , setC1_img] = useState("")
    // const [c2_img , setC2_img] = useState("")
    // const [c3_img , setC3_img] = useState("")
    // const [c4_img , setC4_img] = useState("")
    // const [answer , setAnswer] = useState("")
    // const [lesson , setLesson] = useState("")
    // const [errors , setErrors] = useState("")
    // const [edit , setEdit] = useState("")
  
    const token = localStorage.getItem('token');



  const axiosInstance = axios.create({
    baseURL: `${BASE_URL}`,
});

// const {id} = useParams();
const getQuestion = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await axiosInstance.get(`/api/questions/${id}` , {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json',
            "ngrok-skip-browser-warning": "any",
          },
        });
        // console.log(response.data.data.map(item => item.id))
        console.log(response.data.data);
        // setQuestion(response.data.data)
        setEdit(response.data.data.attributes);
        // setFilteredData(response.data.data);
        const data = response.data.data;
                 return data;
    } catch (error) {
        console.log(error);
    }
}

useEffect (() =>{
  getQuestion();
}, [])

// const handleInputChange = (event) => {
//   const { name, value } = event.target;
//   setEdit({ ...edit, [name]: value });
// };


const { id } = useParams();
const [edit, setEdit] = useState({
  img: "",
  question: "",
  level: "",
  c1: "",
  c2: "",
  c3: "",
  c4: "",
  c1_img: "",
  c2_img: "",
  c3_img: "",
  c4_img: "",
  answer: "",
  lesson: "",
});
const [errors, setErrors] = useState({});

const Navigate = useNavigate();

const handleEdit = async (event) => {
  event.preventDefault();
  try {
    await axiosInstance.patch(
      `/api/questions/${id}`,
      {
        ...edit,
      },
      {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/x-www-form-urlencoded",
          "ngrok-skip-browser-warning": "any",
        },
      }
    );
    setEdit({
      img: "",
      question: "",
      level: "",
      c1: "",
      c2: "",
      c3: "",
      c4: "",
      c1_img: "",
      c2_img: "",
      c3_img: "",
      c4_img: "",
      answer: "",
      lesson: "",
    });

  } catch (e) {
    if (e.response.status === 500) {
      swal({
        title: "   !",
        icon: "success",
        button: "!",
      });
      Navigate("/admin/questiondata");
      console.log(e.response);
      setErrors(e.response.data.errors);
    }
  }
};

 
    
    




    return (
        <>
      <div className="d-flex">
    
       <div className="add-question">
      
      <div className="main-title">     {id}</div>
    
      <Container>
    
          <Row>
         
          <Col lg={12}>
                     <div className="quiz-content">
                    <div className="question">
                      
                        <div className="create-box">
                        <h2 className="title mt-4 mb-4"> </h2>
                        <Form onSubmit={handleEdit}>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
        <Form.Label>* </Form.Label>
        <Form.Control as="textarea" rows={2}  name="question"  value={edit.question}  onChange={(e) => setEdit((prev) => ({ ...prev, question: e.target.value }))}/>
      </Form.Group>

      <Form.Group controlId="formFile" className="mb-3">
        <Form.Label> </Form.Label>
        <Form.Control type="file" name="img"    onChange={(e) => setEdit((prev) => ({ ...prev, img: e.target.value }))}/>
      </Form.Group>

     <div className="chooices d-lg-flex align-items-center mt-5">
     <Form.Group className="mb-3" controlId="formBasicText  ">
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder=" "   name="c1"  value={edit.c1}  onChange={(e) => setEdit((prev) => ({ ...prev, c1: e.target.value }))}/>
      </Form.Group>
     <Form.Group className="mb-3" controlId="formBasicText">
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder=" "  name="c2"  value={edit.c2}  onChange={(e) => setEdit((prev) => ({ ...prev, c2: e.target.value }))}/>
      </Form.Group>
     <Form.Group className="mb-3" controlId="formBasicText ">
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder=" " name="c3"  value={edit.c3}  onChange={(e) => setEdit((prev) => ({ ...prev, c3: e.target.value }))}/>
      </Form.Group>
     <Form.Group className="mb-3" controlId="formBasicText ">
        <Form.Label> </Form.Label>
        <Form.Control type="text" placeholder=" " name="c4"  value={edit.c4}  onChange={(e) => setEdit((prev) => ({ ...prev, c4: e.target.value }))}/>
      </Form.Group>
     </div>
     <div className="chooices  d-lg-flex align-items-center mt-5">
 
     <Form.Group controlId="formFile" className="image mb-3" >
        <Form.Label>  </Form.Label>
        <Form.Control type="file" name="c1_img"  onChange={(e) => setEdit((prev) => ({ ...prev, c1_img: e.target.value }))}/>
      </Form.Group>
     <Form.Group controlId="formFile" className=" image mb-3" >
        <Form.Label>  </Form.Label>
        <Form.Control type="file" name="c2_img"  onChange={(e) => setEdit((prev) => ({ ...prev, c2_img: e.target.value }))}/>
      </Form.Group>
     <Form.Group controlId="formFile" className=" image mb-3" >
        <Form.Label>  </Form.Label>
        <Form.Control type="file" name="c3_img"   onChange={(e) => setEdit((prev) => ({ ...prev, c3_img: e.target.value }))}/>
      </Form.Group>
     <Form.Group controlId="formFile" className=" image mb-3">
        <Form.Label>  </Form.Label>
        <Form.Control type="file" name="c4_img"  onChange={(e) => setEdit((prev) => ({ ...prev, c4_img: e.target.value }))}/>
      </Form.Group>
 
     </div>
    
    <Form.Group className="mt-4">
    <Form.Label >   </Form.Label>
     <Form.Select aria-label="Default select example" className="mt-2" name="answer"  value={edit.answer} onChange={(e) => setEdit((prev) => ({ ...prev, answer: e.target.value }))}>
      <option > </option>
      <option value="1" > </option>
      <option value="2" > </option>
      <option value="3"> </option>
      <option value="4"> </option>
    </Form.Select>
    </Form.Group>



    <Form.Group className="mt-4">
    <Form.Label ></Form.Label>
     <Form.Select aria-label="Default select example" className="mt-2" name="lesson"  value={edit.lesson} onChange={(e) => setEdit((prev) => ({ ...prev, lesson: e.target.value }))}>

      <option > </option>
      <option value="1"> </option>
 
    </Form.Select>
    </Form.Group>
    
   
    
    <Form.Group controlId="formFile" className="mb-3 mt-4">
      <Form.Select aria-label="Default select example" name="level" value={edit.level} onChange={(e) => setEdit((prev) => ({ ...prev, level: e.target.value }))}>
      <option>   </option>
      <option value="1">  </option>
      <option value="2">  </option>
      <option value="3">  </option>
    </Form.Select>
      </Form.Group>


      
        

      <Button variant="primary" type="submit" className="create-button">
        
      </Button>
    </Form>
    </div>
                    </div>
              </div>
               </Col>
          </Row>
      </Container>

    
      </div>
      <div className="sidebar">
       <SidebarAdminMain />
       </div>
       </div>
       </>
    ) 
}

export default EditQuestion;