import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useNavigate, useParams  } from "react-router-dom";
import swal from "sweetalert";
import { BASE_URL } from "../../App";
import '../../styles/quizstudent.css'



const Quiz = () => {
    const navigate = useNavigate()
  const {id} = useParams()
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [quiz, setQuiz] = useState([]);
  const [time , setTime] = useState(0)
  const [selectedAnswers, setSelectedAnswers] = useState([]);
  const [question_id , setQuestion_id] = useState([])
  const [choosen , setChoosen] = useState([])
  const [solvedQuestions , setSolvedQuestions] = useState("")
  const [unsolvedQuestions , setUnsolvedQuestions] = useState("")





  useEffect(() => {
    const token = localStorage.getItem('token');
    const config = {
      headers: {
        'Content-Type': 'application/json',
        "ngrok-skip-browser-warning": "any",
        'Authorization': `Bearer ${token}`,
      }
    };       
    axios.get(`${BASE_URL}/api/quiz-enroll/${id}`, config)
      .then(response => {
        setTime(response.data);
        setQuiz(response.data.data);

        let selectedAnswersFromApi ={}

        response.data.data.forEach((currentObject,index)=>{
          selectedAnswersFromApi[index] = currentObject?.choosen
        })

        let solved = 0
        Object?.keys(selectedAnswersFromApi)?.forEach(key => {
          if (selectedAnswersFromApi[key] != "0"){
            solved ++
          }
         })

        const unsolved = response.data.data.length - solved;
        setSelectedAnswers(selectedAnswersFromApi);
        setChoosen(selectedAnswersFromApi?.[1]);
        setSolvedQuestions(solved);
        setUnsolvedQuestions(unsolved);
      })

      .catch(e => {
        console.error(e);
        if (e.response.status === 401) {
          swal({
                      title: "!     ",
                      icon: "warning",
                      button: "!",
                    });
                    
        }
        else if (e.response.status === 402) {
          swal({
                      title: "!        ",
                      icon: "warning",
                      button: "!",
                    });
        }
        else if (e.response.status === 403) {
          swal({
                      title: "!     ",
                      text: "       ",
                      icon: "warning",
                      button: "!",
                    });
                    navigate(`/modelanswerquiz/${id}`)
        }
        else if (e.response.status === 405) {
          swal({
                      title: "!       ",
                      icon: "warning",
                      button: "!",
                    });
        }
      });
  }, [])



  const handleNextQuestion = async (e) => {
    e.preventDefault();
    setCurrentQuestion(currentQuestion + 1);
    setChoosen(selectedAnswers[currentQuestion + 1] || "");
  };
  
  const handlePrevQuestion = (e) => {
    e.preventDefault();
    setCurrentQuestion(currentQuestion - 1);
    setChoosen(selectedAnswers[currentQuestion - 1] || "");
  };

  const handleFinish = (e) => {
    e.preventDefault();
      navigate(`/modelanswerquiz/${id}`)
  };

   const handleAnswerSelected = (e) => {
      const selectedAnswer = e.target.value;
      const selectedAnswersCopy = {...selectedAnswers};
      selectedAnswersCopy[currentQuestion] = selectedAnswer;
      setSelectedAnswers(selectedAnswersCopy);
      setChoosen(selectedAnswer);
 };



  const token = localStorage.getItem('token');
  const axiosInstance = axios.create({
    baseURL: `${BASE_URL}`,
    headers: {
        'Content-Type': 'application/json',
        "ngrok-skip-browser-warning": "any",
        Authorization: `Bearer ${token}`,
    },
  });

 const handleSubmitQuestion = async(e , question_id , callbackFunction) => {
  e.preventDefault()
  console.log(question_id);
  console.log(choosen);
     try {
   const response =  await axiosInstance.post(`/api/submit-quiz-question/${id}/${question_id}` , {
     choosen
      });
         setChoosen("");
        if (callbackFunction) {
          callbackFunction(e);
        }

      console.log(response);

      let solved = 0
      for(const key in selectedAnswers){
        if (selectedAnswers[key] && selectedAnswers[key] != 0){
          solved ++
        }
      }
      const unsolved = quiz.length - solved;
      setSolvedQuestions(solved);
      setUnsolvedQuestions(unsolved); 

    } catch(e) {
      if (e.response.status === 401) {
        swal({
                    title: "!     ",
                    icon: "warning",
                    button: "!",
                  });
                  navigate("/")
      }
      else if (e.response.status === 402) {
        swal({
                    title: "!        ",
                    icon: "warning",
                    button: "!",
                  });
      }
      else if (e.response.status === 403) {
        swal({
                    title: "!    ",
                    icon: "warning",
                    button: "!",
                  });
                  navigate("/")
      }
      else if (e.response.status === 405) {
        swal({
                    title: "!       ",
                    icon: "warning",
                    button: "!",
                  });
      }
      }
    }


 function CountdownTimer({ remainingTime }) {
  const [timeLeft, setTimeLeft] = useState(remainingTime);

  useEffect(() => {
    const remainingTime = time.message;
    const timer = setTimeout(() => {
      if (remainingTime <= 0) {
        navigate(`/modelanswerquiz/${id}`);
      } else {
        setTime({ message: remainingTime - 1 });
      }
      }, 1000);
      return () => clearTimeout(timer);
    }, [time, currentQuestion]);

    const hours = Math.floor(timeLeft / 3600);
    const minutes = Math.floor((timeLeft % 3600) / 60);
    const seconds = timeLeft % 60;

  return (
    <div>
      {hours} :{minutes < 10 ? "0" : ""}   {minutes} : {seconds < 10 ? "0" : ""}  {seconds}
    </div>
  );
}

  return (
    <div class="quiz-main">
      <Container>
        <Row>
          <Col lg={12}>
            <h2 className="main-title mt-5"> </h2>
            <div className="quiz-app ">
                <div className="quiz-data">
                <div className="timer">
                  <span> :      <img src="/images/clock.png"  alt=""/> </span>
                  <span><CountdownTimer remainingTime={time.message} /></span>
            </div>
                      <div className="question-tabs">
           {quiz.map((q, index) => (
    <div key={q.id} className={`question-tab ${q.choosen ? 'answered' : ''} ${currentQuestion === index ? 'active' : ''}`} onClick={() => setCurrentQuestion(index)}>
        {index + 1}
    </div>
        ))}
            
          </div>
              <div className="quiz-info">
                    <div className="content">
                    <p> :     </p>
                    <span className="length">{quiz.length}</span>
                    </div>
                    <div className="content">
                  <p>  :      </p>
                    <span className="unsolved">{unsolvedQuestions ? unsolvedQuestions : "0"}</span>
                  </div>
              <div className="content">
              <p>:    </p>
              <span className="solved"> {solvedQuestions ? solvedQuestions : "0"}</span>
              </div>
                
              </div>

              <div className="quiz-control">
              <button  className="prev"  onClick={(e) =>handleSubmitQuestion(e,quiz[currentQuestion].id, handlePrevQuestion)} disabled={currentQuestion === 0}></button>
               <button  className="next"  onClick={async (e) => {await handleSubmitQuestion( e,quiz[currentQuestion].id,handleNextQuestion); }} disabled={currentQuestion === quiz.length - 1}></button>
              </div>
              <div className="quiz-finish">
              {currentQuestion === quiz.length - 1 && (
            <button type="submit" className="finish" onClick={(e)=> handleFinish(e) | handleSubmitQuestion(e , quiz[currentQuestion].id)}> </button>
          )}
              </div>

                </div>

{quiz.length > 0 && (
        <form className="form-quiz">
         <div>
    </div>
          <div className="exam">

          <h3 className="question"><span> <img src="/images/problem.png" className="icon-question" alt=""/></span>{quiz[currentQuestion].question}</h3>  
          <img  src={`${BASE_URL}/storage/${quiz[currentQuestion].img}`} className="img-question" alt=""/>    
          <div className="answers d-flex">
            <label>
              <input type="checkbox" name="choosen" value="1"    checked={selectedAnswers[currentQuestion] === "1"} onChange={(e) => handleAnswerSelected(e) | setChoosen(e.target.value)} />
              {quiz[currentQuestion].c1} 
              {/* <img src={`${BASE_URL}/storage/${quiz[currentQuestion].c1_img}`} className="img-question" alt=""/>     */}

            </label>
            <label>
              <input type="checkbox" name="choosen" value="2"  checked={selectedAnswers[currentQuestion] === "2"}   onChange={(e) => handleAnswerSelected(e) | setChoosen(e.target.value)} />
              {quiz[currentQuestion].c2} 
              {/* <img src={`${BASE_URL}/storage/${quiz[currentQuestion].c2_img}`} className="img-question" alt=""/>     */}

            </label>
            <label>
              <input type="checkbox" name="choosen" value="3"  checked={selectedAnswers[currentQuestion] === "3"} onChange={(e) => handleAnswerSelected(e) | setChoosen(e.target.value)} />
              {quiz[currentQuestion].c3} 
              {/* <img src={`${BASE_URL}/storage/${quiz[currentQuestion].c3_img}`} className="img-question" alt=""/>     */}

            </label>
            <label>
              <input type="checkbox" name="choosen" value="4"  checked={selectedAnswers[currentQuestion] === "4"}  onChange={(e) => handleAnswerSelected(e) | setChoosen(e.target.value)} />
              {quiz[currentQuestion].c4} 
              {/* <img src={`${BASE_URL}/storage/${quiz[currentQuestion].c4_img}`} className="img-question" alt=""/>     */}

            </label>
          </div>
          </div>
          <br />
        </form>
      )}
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};
export default Quiz;
