import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { useNavigate } from "react-router";
import { Link } from "react-router-dom";
import swal from "sweetalert";
import { BASE_URL } from "../App";





export const Loginuser = ({setLoggedIn , loggedIn, clearToken }) => {
       const navigate = useNavigate();

     const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [errors, setErrors] = useState("");

    const handleLogin = async (event) => {
        event.preventDefault();
    try {
        await axios.post(`${BASE_URL}/api/login` , {email , password})
        .then(response => {
            const token = response.data.data.token;
            const role = response.data.data.user.role;
            console.log(response.data.data.user.role);
              localStorage.setItem("token", token);
              localStorage.setItem("sha1", role);
              setLoggedIn(true)
              if (role > 0) {
                console.log("admin");
                navigate("/admin");
              } else {
                navigate("/");
                console.log("user");
              }
          })
        setEmail("");
        setPassword("");
        swal({
            title: "   !",
            icon: "success",
            button: "!",
          });
            // navigate("/")
          } catch (e) {


             if(e.response.status === 422 ) {
                setErrors(e.response.data.errors)
             
              }
            else if (e.response.status === 401) {
            swal({
                title: "!     ",
                text: "      ",
                icon: "warning",
                button: "!  ",
              });
        }
        else if (e.response.status === 402) {
            swal({
                title: "      ",
                text: "   ",
                icon: "warning",
                button: "!  ",
              });
        }
    }
    }
  
    
return (
    <>
    <div className="register-user pb-5 pt-5">
    <div className="main-title"> </div>
        <Container className="mt-auto ms-auto mb-auto me-0">
            <Row>
                <Col lg={8}>
                <div className="box">
                <Form onSubmit={handleLogin}>
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>  </Form.Label>
        <Form.Control type="number" placeholder="  " name="email" required onChange={(e) =>setEmail(e.target.value)}/>
        <Form.Text className="text-muted">
        {errors.email && <div className="error">{errors.email[0]}</div>}
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label> </Form.Label>
        <Form.Control type="password" placeholder="  " name="password" required onChange={(e) =>setPassword(e.target.value)}/>
        <Form.Text className="text-muted">
        {errors.password && <div className="error">{errors.password[0]}</div>}
        </Form.Text>
      </Form.Group>

 
       <Button variant="outline-primary" className="mt-3 mb-3" type="submit"> </Button>{' '}
      <Link to="/registeruser">!  </Link>
    </Form>
                </div>
                </Col>
            </Row>
        </Container>
    </div>
    </>
)
}

export default Loginuser;